.registration {
  background-image: url(../images/registration/registration-mobile.png);
  background-color: $black-color;

  min-width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;

  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;

  text-align: center;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/registration/registration-mobile@2x.png);
  }

  @media screen and (min-width: 480px) {
    background-image: url(../images/registration/registration-tablet.png);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/registration/registration-tablet@2x.png);
    }
  }

  @media screen and (min-width: 768px) {
    max-width: 1600px;
    background-image: url(../images/registration/registration-desktop.png);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/registration/registration-desktop@2x.png);
    }
  }
}

.text-accent {
  color: $accent-text-color;
}

.registration-block {
  padding-top: 50px;
  padding-bottom: 50px;

  @media screen and (min-width: 1345px) {
    padding-top: 100px;
    padding-bottom: 62px;
  }
}

.registration-block-title {
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 1.357;
  letter-spacing: 0.03em;
  color: $white-color;

  @media screen and (min-width: 768px) {
    font-weight: 800;
    font-size: 26px;
    line-height: 1.3;
  }

  @media screen and (min-width: 1345px) {
    font-weight: 800;
    font-size: 46px;
    line-height: 1.3;
  }
}

.registration-block-text {
  padding-top: 30px;

  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.08em;

  color: $white-color;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.21;
  }

  @media screen and (min-width: 1345px) {
    padding-top: 50px;

    font-size: 30px;
    line-height: 1.23;
  }
}

// форма регистрации
.form {
  padding-top: 30px;
  width: 100%;
  max-width: 390px;

  margin-left: auto;
  margin-right: auto;
  &-field {
    position: relative;

    width: 100%;

    margin-bottom: 30px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 40px;
  }
  @include adaptive(1345px) {
    max-width: 478px;
  }
}

.form-field-label {
  position: absolute;

  top: 50%;
  left: 28px;
  color: $white-color;

  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.214;

  display: flex;
  align-items: center;
  letter-spacing: 0.08em;

  transform: translateY(-50%);
  transition: $fast cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 1345px) {
    font-size: 20px;
    line-height: 1.2;
  }
}

.form-field-input {
  width: 100%;
  background-color: rgba($color: #000000, $alpha: 0.1);

  height: 48px;
  border: 1px solid $white-color;

  padding-left: 28px;
  color: $accent-text-color;

  &:hover,
  &:focus,
  &:active {
    border: 1px solid $accent-color;
    color: $white-color;
  }

  &:hover + .form-field-label,
  &:focus + .form-field-label,
  &:not(:placeholder-shown) + .form-field-label {
    transform: translate(-25px, -45px);
    color: $accent-text-color;

    @media screen and (min-width: 1345px) {
      transform: translate(-25px, -63px);
    }
  }

  @media screen and (min-width: 1345px) {
    height: 70px;
  }
}

.form-btn {
  margin-top: 50px;

  @media screen and (min-width: 768px) {
    margin-top: 31px;
  }

  @media screen and (min-width: 1345px) {
    margin-top: 80px;
  }
}
